import React from 'react';


class Square extends React.Component {

    /*
    Nur bei jedem Click auf einem Square, wird der Value auf X gesetzt.
     */

    render() {


        return (
            <div>
                <button className="squares"
                        onClick={() => {
                            this.props.passedFunction()
                        }}>
                    {this.props.value}
                </button>
            </div>

        );
    }
}

export default Square;